
.custom-keyboard {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: absolute;
    bottom: 0;
    flex-direction: column;
    align-items: center;
    transition: bottom 0.3s;
}

.custom-keyboard.hide {
    bottom: calc((-25vh + 86px));
}


.custom-keyboard-row {
    display: flex;
}

.custom-keypad {
    color: var(--font1);
    font-size: 20px;
    width: calc(10vw - 2px);
    max-width: 64px;
    height: calc((25vh - 90px)/2);
    line-height: calc((25vh - 90px)/2);
    background-color: var(--theme1s);
    text-align: center;
    cursor: pointer;
    border: 1px solid;
    border-top: none;
    border-left-color: var(--theme1s);
    border-right-color: var(--theme1s);
    border-bottom-width: 2px;
    transition: color 0.3s;
}


.App:has(.output.focus) .custom-keypad {
    color: var(--font2);
}

.custom-keyboard-row.show .custom-keypad {
    width: 30vw;
    max-width: none;
    height: calc((25vh - 90px)/3);
    line-height: calc((25vh - 90px)/3);
    border-radius: 6px 6px 0 0;
    box-shadow: 0 0 1px;
}

.custom-keypad:hover, .custom-keypad:active {
    background-color: var(--theme2);
}


@media screen and (max-width: 768px) {    
    .custom-keyboard-row.show .custom-keypad {
        display: none;
    }
}