.currency-overlay {
    width: 100vw;
    height: 100vh;
    position: absolute;
    background-color: none;
    top: 0;
    display: flex;
    justify-content: center;
    z-index: -1;
    transition: 0.2s ease-out;
}

.currency-overlay.show {
    z-index: 1;
    background-color: rgba(0,0, 0, 45%);
}



.show .currency-panel.large {
    margin-top: 0vh;
    height: 100vh;
}


.show .currency-panel {
    margin-top: 45vh;
}

.currency-panel {    
    width: 80vw;
    min-width: 400px;
    overflow: hidden;
    height: 55vh;
    min-height: 300px;
    background-color: var(--theme2);
    border-radius: 25px 25px 0 0;
    box-shadow: 0px 0px 4px var(--theme2);
    margin-top: 120vh;
    transition: margin-top 0.2s, height 0.2s;
}

.currency-navbar {
    width: calc(100% - 60px);
    height: 96px;
    display: flex;
    font-size: 32px;
    justify-content: space-between;
    align-items: center;
    color: var(--font1);
    border-bottom: 3px solid var(--theme1);
    border-radius: 25px 25px 0 0;
    box-shadow: 0px 0px 4px var(--theme2);
    padding: 0 30px 0 30px;
}

.currency-navbar.out {    
    color: var(--font2);
}

.currency-navbutton {
    cursor: pointer;
    padding: 6px 10px 6px 10px;
    border-radius: 9px;
    outline: 2px solid;
}


.currency-navbutton:hover, .currency-navtitle:hover {
    opacity: 0.7;
}

.currency-navbutton:active, .currency-navtitle:active {
    opacity: 0.4; 
}

.currency-navtitle {
    /*cursor: pointer;*/
    padding: 8px 16px 12px 16px;
    outline: 2px solid;
    font-weight: 500;
    font-size: 38px;
    line-height: 38px;
    box-shadow: 0 0 3px var(--font2);
    border-radius: 3px;
}

.currency-page {
    width: 200%;
    height: calc(100% - 96px);
    display: flex;
    transition: margin-left 0.5s;
}

.currency-page.more {    
    margin-left: -100%;
}

.currency-list {
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: auto;
}

.currency-option {
    cursor: pointer;
    text-align: center;
    color: var(--font0);
    font-size: 32px;
    line-height: 32px;
    padding: 12px 0 12px 0;
    margin: 4px 0 4px 10%;
    width: 80%;
    position: relative;
}

.currency-option::after {
    content: attr(region);
    position: absolute;
    width: 35vw;
    font-size: 20px;
}


.currency-option:hover {
    opacity: 0.8;
    background-color: var(--theme1);
}

.currency-option:active {
    opacity: 0.6;
}

.currency-option.bookmarked {
    color: var(--font1);
}

.currency-panel:has(.currency-navbar.out) .currency-option.bookmarked {
    color: var(--font2);
}

@media screen and (max-width: 768px) {
    .show .currency-panel {
        margin-top: 30vh;
    }

    .currency-panel {
        width: 100vw;
        height: 70vh;
    }

    .currency-navbar {
        height: 64px;
        font-size: 26px;
        justify-content: space-evenly;
    }
    
    .currency-page {
        height: calc(100% - 64px);
    }

    .currency-option {
        overflow: hidden;
        border-bottom: 1px solid;
    }

    .currency-option::after {
        font-size: 16px;
        top: 0;
    }

}