.ex-panel {
    background-color: var(--theme2);
    border-radius: 13px;
    width: 80%;
    height: 70%;
    font-size: 3em;
    padding: 15px;
    caret-color: var(--white);
    letter-spacing: 1px;
    overflow: hidden;
    transition: 0.3s;
    box-shadow: 0px 2px 0px var(--font0);
}

.ex-panel:hover {
    box-shadow: 0px 2px 0px;
}

.ex-panel:focus, .App:has(.custom-keyboard:hover) .ex-panel.focus {
    outline: 0px transparent;
    box-shadow: 0px 4px 0px;
}


.ex-panel.input {
    color: var(--font1);
}

.ex-panel.output {
    color: var(--font2);
}

.ex-panel-div {
    height: 100%;
    width: 50vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}

.ex-panels {
    height: 45vh;
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.ex-pairs {
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
}

.currency-item {
    color: var(--font0);
    font-size: 2.5em;
    line-height: 2em;
    width: 12vw;
    text-align: center;
    cursor: pointer;
    background-color: var(--theme2);
    transition: 0.2s;
}


.currency-item:hover, .App:has(.input.focus) .currency-item.inpair {
    background-color: var(--theme1);
    outline: 3px solid var(--theme2); 
    box-shadow: 0 0 6px var(--theme1s);
    color: var(--font1);
}

.currency-item.outpair:hover, .App:has(.output.focus) .currency-item.outpair {
    background-color: var(--theme1);
    outline: 3px solid var(--theme2); 
    box-shadow: 0 0 6px var(--theme1s);
    color: var(--font2);
}


.currency-item:active {
    background-color: var(--theme1s);
    box-shadow: none;
    opacity: 0.8;
}

@media screen and (max-width: 768px) {
    .ex-panels {
        flex-direction: column;
        height: 70vh;
        margin-top: 2vh;
    }

    .ex-panel {
        height: calc(30vh - 30px);
        width: calc(85vw - 30px);
        /*caret-color: transparent;*/
        text-shadow: 0 0 0 #333333;
    }/*
    
    .ex-panel.focus {
        outline: 0px transparent;
        box-shadow: 0px 0px 5px var(--white);
    }*/

    .currency-item {
        font-size: 1.5em;
        line-height: 3em;
        width: 27vw;
        outline: 2px solid;
    }

    .ex-pairs {
        margin-top: 14px;
        justify-content: center;
    }

    .ex-panels:focus{
        position: fixed;
        top:50px;
    }
}